import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { Route } from 'react-router-dom'

import Article from './Article'
import Header from '../components/Header'
import Loading from '../components/Loading'


const PropsRoute = ({ component, ...rest }) => {
	return (
		<Route {...rest} render={routeProps => {
			return renderMergedProps(component, routeProps, rest)
		}} />
	);
}
const renderMergedProps = (component, ...rest) => {
	const finalProps = Object.assign({}, ...rest)
	return (
		React.createElement(component, finalProps)
	);
}

class Articles extends Component {
	constructor(props) {
		super(props)
		this.state = {
			channelData: [],
			loading: true,
			city: null,
			url: null,
			channelInfo: [{
				categoryTitle: ""
			}]
		}
		this.setStateUrl = this.setStateUrl.bind(this);
	}
	setStateUrl = (childStateUrl) => {
		this.setState({url: childStateUrl})
		//console.log('Url changed to ' + childStateUrl);
	}
	loadData = (city) => {
		this.setState({ loading: true })
		fetch("https://huba.sk/index.php/API/" + city)
			//		fetch("/API/"+ this.props.match.params.channel + "/index.json")
			.then(results => {
				return results.json();
			})
			.then(data => {
				const channelData = data.channelData.entries;
				const channelInfo = {
					categoryTitle: data.channelData.categoryTitle
				};
				this.setState({ channelData, channelInfo });
				document.title = data.channelData.categoryTitle + " | Komunálne voľby 2018";
			})
		this.setState({ loading: false, city: city, url: "/" + city })
		//console.log('Articles loadData from /' + city )
	}


	componentDidMount() {
			return this.state.url !== this.props.match.url ? this.loadData(this.props.match.params.city):""
	}
	componentWillReceiveProps(nextProps) {
		//return this.state.url !== nextProps.match.url ? this.loadData(nextProps.match.params.city):""
		this.loadData(nextProps.match.params.city)
	}
	render() {
		if (!this.props.match.isExact) {
			return <PropsRoute path={`${this.props.match.url}/:url_title`} component={Article} city={this.props.match.params.city}  setParentState={this.setStateUrl}/>
		}
		return (
			<div className="flex">
				<PropsRoute path={`${this.props.match.url}/:url_title`} component={Article} city={this.props.match.params.city} setParentState={this.setStateUrl}/>
				<Header title={this.state.channelInfo.categoryTitle} {...this.props} />
				<div className={this.props.match.params.city + " bg"}>
					<div className="articles page-container container">
						{this.state.loading && <Loading />}
						{this.state.channelData.map((channelEntry) => {
							return (
								<div key={channelEntry.url_title} className="channelEntry">
									<h2>{channelEntry.cislo > 0 && <span className="cislo">{channelEntry.cislo}</span>}{channelEntry.url_title ? <Link to={this.props.location.pathname + "/" + channelEntry.url_title}>{channelEntry.title}</Link> : channelEntry.title}</h2>
									<p className={channelEntry.cislo > 0 ? "space small" : ""}>{channelEntry.summary}</p>
									{channelEntry.images && channelEntry.images.map(
										(image, key) => {
											return (
												<img key={key} src={image.image} alt={image.desc} />
											)
										}
									)}
								</div>
							)
						}
						)}
					</div>
				</div>
			</div>
		);
	}
}

export default Articles;



