import React, { Component } from 'react'
import Button from '@material-ui/core/Button'
import IconButton from '@material-ui/core/IconButton'
import Checkbox from '@material-ui/core/Checkbox'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import ArrowForward from '@material-ui/icons/ArrowForward'
import ArrowBack from '@material-ui/icons/ArrowBack'
import ReactGA from 'react-ga'
import DoneIcon from '@material-ui/icons/Done'
import ClearIcon from '@material-ui/icons/Clear'

import ProgressBar from './ProgressBar'
import Results from './Results'

class Questionaire extends Component {
  constructor(props) {
    super(props)
    this.state = {
      answers: [],
      answers_compare: [],
      currentIndex: 0,
      progress: (1 / props.questions.length) * 100,
      total: props.questions.length
    }
  }
  loadOdpovede = (city) => {
    fetch("https://huba.sk/index.php/API/kalkulacka/" + city)
      //		fetch("/API/"+ this.props.match.params.channel + "/" + this.props.match.params.url_title + ".json")
      .then(results => {
        return results.json();
      })
      .then(data => {
        const answers_compare = data.channelData.entries;
        const channelInfo = {
          categoryTitle: data.channelData.categoryTitle
        };
        this.setState({ answers_compare, channelInfo });
      })
  }
  clickButt = (ev) => {

    var disabled = ev.target.getAttribute("data-disabled")
    if (disabled === "true") return null
    var lastAnswers = this.state.answers
    var tmp = lastAnswers[(ev.target.id - 1)]
    if (!tmp) tmp = {}
    tmp["questionID"] = parseInt(ev.target.id, 10)
    tmp["answer"] = ev.target.value
    tmp["answerID"] = parseInt(ev.target.getAttribute("data-id"), 10)
    lastAnswers[(ev.target.id - 1)] = tmp
    this.setState({
      answers: lastAnswers
    })
    var _tmpEvent = {}
    _tmpEvent["category"] = 'Kalkulačka ' + this.props.cityName
    _tmpEvent["action"] = ev.target.value
    _tmpEvent["label"] = this.props.cityName + ' | ' + ev.target.id
    ReactGA.event(_tmpEvent)
    setTimeout(this.goNext, 1000)
  }
  clickImportant = (ev) => {
    var lastAnswers = this.state.answers
    var tmp = lastAnswers[(ev.target.value - 1)]
    if (!tmp) tmp = {}
    tmp["important"] = ev.target.checked
    lastAnswers[(ev.target.value - 1)] = tmp
    this.setState({
      answers: lastAnswers
    })
    if (ev.target.checked) {
      var _tmpEvent = {}
      _tmpEvent["category"] = 'Kalkulačka ' + this.props.cityName
      _tmpEvent["action"] = 'important'
      _tmpEvent["label"] = this.props.cityName + ' | ' + ev.target.value
      ReactGA.event(_tmpEvent)
    }
  }
  clickNext = (ev) => {
    this.goNext()
  }
  clickPrev = (ev) => {
    this.goPrev()
  }
  goNext = () => {
    var lastIndex = this.state.currentIndex
    lastIndex++
    var progressNow = ((lastIndex + 1) / this.props.questions.length) * 100
    progressNow = (progressNow > 100) ? 100 : progressNow
    this.setState({
      currentIndex: lastIndex,
      progress: progressNow
    })
    document.getElementById("questions").style.transform = 'translateX(' + -(lastIndex * 100) + '%)'
  }
  goTo = (i) => {
    var progressNow = ((i + 1) / this.props.questions.length) * 100
    this.setState({
      currentIndex: i,
      progress: progressNow
    })
    document.getElementById("questions").style.transform = 'translateX(' + -(i * 100) + '%)'
  }
  goPrev = () => {
    var lastIndex = this.state.currentIndex
    lastIndex--
    var progressNow = ((lastIndex + 1) / this.props.questions.length) * 100
    this.setState({
      currentIndex: lastIndex,
      progress: progressNow
    })
    document.getElementById("questions").style.transform = 'translateX(' + -(lastIndex * 100) + '%)'
  }
  componentDidMount = () => {
    this.loadOdpovede(this.props.city)
  }
  render() {
    return (
      <div className="questionaire">
        <ProgressBar progress={this.state.progress} />
        <ul id="questions">
          {this.props.questions.map((question, key) => {
            var numberSign = '<span class="number"><b>' + question.id + '</b>/' + this.props.questions.length + '</span>'
            return (
              <li key={key} style={{ "left": key * 100 + "%" }}>
                <div className="content" dangerouslySetInnerHTML={{ __html: numberSign + question.otazka }} />
                <div className="answers">
                  {this.props.city !== "home" && <div className="is-important">
                    <FormControlLabel
                      control={
                        <Checkbox
                          onChange={this.clickImportant.bind(this)}
                          value={question.id}
                        />
                      }
                      label="Táto otázka je pre mňa dôležitá"
                    />
                  </div>}
                  <div className="prevnext">
                    {this.state.currentIndex > 0 &&
                      <IconButton aria-label="previuos" onClick={this.clickPrev.bind(this)}>
                        <ArrowBack />
                      </IconButton>
                    }
                    {this.state.currentIndex < (this.state.total) &&
                      <IconButton aria-label="next" onClick={this.clickNext.bind(this)}>
                        <ArrowForward />
                      </IconButton>}
                  </div>
                  {question.moznosti.map((m, i) => {
                    var color = "default"
                    var icon = null
                    var disabled = false
                    if (m === "Áno") {
                      color = "primary"
                    } else if (m === "Nie") {
                      color = "secondary"
                    }
                    var variant = "text"
                    if (this.state.answers[question.id - 1] && this.state.answers[question.id - 1]["answer"]) {
                      if (m === this.state.answers[question.id - 1]["answer"]) {
                        variant = "outlined"
                      }
                      if (this.props.city === "home" && m === this.state.answers[question.id - 1]["answer"]) {
                        if (i === this.state.answers_compare[0]["answers"][question.id - 1]["answerID"]) {
                          icon = <DoneIcon />
                          color = "primary"
                        } else {
                          icon = <ClearIcon />
                          color = "secondary"
                        }
                      } else if (this.props.city === "home" && i === this.state.answers_compare[0]["answers"][question.id - 1]["answerID"]) {
                        icon = <DoneIcon />
                        color = "primary"
                      }
                      if (this.props.city === "home") disabled = true
                    }

                    return (
                      <Button key={i} data-id={i} data-disabled={disabled} id={question.id} onClick={this.clickButt.bind(this)} value={m} variant={variant} color={color}>{m}{icon}</Button>
                    )
                  })}
                </div>
              </li>
            )
          })}
          <li style={{ "left": (this.state.total) * 100 + "%" }} className="results">
            <Results total={this.state.total} answers={this.state.answers} answers_compare={this.state.answers_compare} city={this.props.city} questions={this.props.questions}/>
          </li>
        </ul>
      </div>
    )
  }
}

export default Questionaire;



