import React, { Component } from 'react'

class ProgressBar extends Component {
  static get defaultProps() {
    return {
      progress:0
    }
  }
  render() {
    return (
        <div id="progressBar" style={{"width": this.props.progress + "%"}}></div>
    );
  }
}

export default ProgressBar;



