import React, { Component } from 'react'
import {Link} from 'react-router-dom'

class Footer extends Component {
  render() {
    var currentPath = this.props.match.params.city
    return (
        <footer className="App-footer">
          <nav>
            <ul>
              <li className={"tab" + (currentPath === "zvolen" ? " active" : "")}><Link to="/zvolen">Zvolen</Link></li>
              <li className={"tab" + (currentPath === "bzovik" ? " active" : "")}><Link to="/bzovik">Bzovík</Link></li>
              <li className={"tab" + (currentPath === "sv-anton" ? " active" : "")}><Link to="/sv-anton">Sv.Anton</Link></li>
              {/* <li className={"tab" + (currentPath === "jelsava" ? " active" : "")}><Link to="/jelsava">Jelšava</Link></li> */}
              <li className={"tab" + (currentPath === "dobra_niva" ? " active" : "")}><Link to="/dobra_niva">Dobrá Niva</Link></li>
            </ul>
          </nav>
        </footer>
    );
  }
}

export default Footer;



