import React, { Component } from 'react'
import Modal from '@material-ui/core/Modal'
import DoneIcon from "@material-ui/icons/Done"
import CloseIcon from "@material-ui/icons/Close"
//import { Link } from 'react-router-dom'


function getModalStyle() {
    const top = 50;
    const left = 50;

    return {
        top: `${top}%`,
        left: `${left}%`,
        transform: `translate(-${top}%, -${left}%)`,
        padding: 20,
        maxHeight: "90%",
        overflow: "scroll",
        backgroundColor: "white",
        position: "absolute",
        width: "90%",
        maxWidth: 800,
        height: "100%"
    };
}


function sortfunction(a, b) {

    var s1 = a["score"];
    var s2 = b["score"];

    var c1 = a["cislo"];
    var c2 = b["cislo"];

    if (s1 < s2) return 1;
    if (s1 > s2) return -1;
    if (c1 < c2) return -1;
    if (c1 > c2) return 1;
    return 0;
}
var DisplayAnswers = (props) => {
    return (
        <div className="modalScroll">
            <div className="thead">
                {props.questions.map((odpoved, i) => {
                    var odp_kandidat
                    var odp_moja = props.answers[i] ? odpoved.moznosti[props.answers[i]["answerID"]] : "-"
                    return (
                        <div className="tr" key={i}>
                            <div key={i} className="th long" dangerouslySetInnerHTML={{ __html: odpoved.otazka }} ></div>
                            <div className="th">{props.answers_compare.map((odpoved_kandidat) => {
                                if (odpoved_kandidat.cislo === props.kandidat) {
                                    odp_kandidat = odpoved.moznosti[odpoved_kandidat.answers[i]["answerID"]]
                                    return odp_kandidat
                                } else {
                                    return null
                                }
                            }
                            )}</div>
                            <div className="th">
                                {odp_moja = props.answers[i] ? odpoved.moznosti[props.answers[i]["answerID"]] : "-"}
                            </div>
                            <div className="th">{odp_moja === odp_kandidat ? <div><DoneIcon color="primary"/> <span className="cover primary"/></div>: (odp_moja !== "-" && odp_moja !== "Neviem") ? <div><CloseIcon color="secondary"/> <span className="cover secondary"/></div>: "-"}</div>
                        </div>
                    )
                })}
            </div>
        </div>
    )
    //return "Kandidat cislo " + props.kandidat

}
var DisplayResults = (props) => {
    if (props.city === "home") { //jedna sa o klasicky kviz
        if (props.target.state.results.length === 1) {
            var res = props.target.state.results[0]
            return (<h3 key={0}>{res.title} {res.matches} / {props.target.props.total}</h3>)
            // return (<h3 key={0}>{res.title} {Math.ceil((res.matches / props.target.props.total) * 100) + "%"}</h3>)
        } else {
            return null
        }
    } else { //pocita sa podla pravidiel kalkulacky
        if (props.target.state.results.length > 0) {
            var tmpRes = props.target.state.results
            tmpRes.sort(sortfunction)
            //            tmpRes.sort(function (a, b) { return b.score - a.score })
            return (
                tmpRes.map((res, i) => {
                    return (
                        <div className={res.answered > 0 ? "channelEntry" : "channelEntry empty"} key={i}>
                            <h2>
                                <span className="result">{res.score}%</span>
                                <span className="cislo">{res.cislo}</span>
                                {res.answered > 0 ?
                                    // <Link to={res.url}>{res.title}</Link>
                                    <a onClick={props.target.openOdpovede.bind(this)} id={i}>{res.title}</a>
                                    :
                                    <a>{res.title}</a>
                                }
                            </h2>
                            <p className="space small">{res.summary}</p>
                            {props.target.state.currentOpenResult === i &&
                                <Modal
                                    open={props.target.state.modalOpen}
                                    onClose={props.target.handleClose}
                                    className="modal">
                                    <div style={getModalStyle()}>
                                        <div style={{ paddingLeft: 50 }}>
                                            <div className="channelEntry">
                                                <h2>
                                                    <span className="result">{res.score}%</span>
                                                    <span className="cislo">{res.cislo}</span>
                                                    {res.answered > 0 ?
                                                        // <Link to={res.url}>{res.title}</Link>
                                                        <a onClick={props.target.openOdpovede.bind(this)} id={i}>{res.title}</a>
                                                        :
                                                        <a>{res.title}</a>
                                                    }
                                                </h2>
                                                <p className="space small">{res.summary}</p>
                                            </div>
                                        </div>
                                        <div className="thead">
                                            <div className="tr">
                                                <div className="th long">Otázka</div>
                                                <div className="th">{res.title}</div>
                                                <div className="th">Ja</div>
                                                <div className="th">Zhoda</div>
                                            </div>
                                        </div>
                                        <DisplayAnswers {...props.target.props} kandidat={res.cislo} />
                                    </div>
                                </Modal>
                            }
                        </div>
                    )
                })
            )
        } else {
            return null
        }
    }
}
class Results extends Component {
    constructor(props) {
        super(props)
        this.state = {
            results: [],
            currentOpenResult: 0,
            modalOpen: false
        }
        this.openOdpovede = this.openOdpovede.bind(this);
    }
    calculateResults = (props) => {
        if (props.city === "home") return this.calculateQuestionaire(props)
        if (props.answers_compare.length && props.answers.length) {
            var tmpResults = []
            var multiplier = 2
            props.answers_compare.map((kandidat) => {
                var matches = 0, answered = 0, addPoints = 0, addAnswers = 0, score = 0
                kandidat.answers.map((odpoved, i) => {
                    if (typeof props.answers[i] !== 'undefined') {
                        if (typeof props.answers[i]["answerID"] !== 'undefined') {
                            if (odpoved.answerID === 1 || props.answers[i]["answerID"] === 1) {//kandidat neodpovedal alebo ja som neodpovedal
                                //console.log("kandidat " + kandidat.title + " neodpovedal alebo ja som neodpovedal")
                            } else if (odpoved.answerID === props.answers[i]["answerID"]) { //kanddat sa trafil a ja som odpovedal
                                addPoints = props.answers[i]["important"] ? multiplier : 1
                                addAnswers = props.answers[i]["important"] ? multiplier : 1
                                matches += addPoints
                                answered += addAnswers
                                //console.log("kanddat " + kandidat.title + " sa trafil a ja som odpovedal")
                            } else {//kanddat sa netrafil a ja som odpovedal
                                addPoints = props.answers[i]["important"] ? -multiplier : -1
                                addAnswers = props.answers[i]["important"] ? multiplier : 1
                                matches += addPoints
                                answered += addAnswers
                                //console.log("kanddat " + kandidat.title + " sa netrafil a ja som odpovedal")
                            }
                        }
                    }
                    return null
                })
                score = parseInt(((matches / (2 * answered)) + 0.5) * 100, 10)
                score = isNaN(score) ? 0 : score
                tmpResults.push({
                    "title": kandidat.title,
                    "matches": matches,
                    "answered": answered,
                    "url": kandidat.url,
                    "summary": kandidat.summary,
                    "cislo": kandidat.cislo,
                    "score": score
                })
                return null
            })
            return this.setState({ results: tmpResults })
        }
    }
    calculateQuestionaire = (props) => {
        if (props.answers_compare.length && props.answers.length) {
            var tmpResults = []
            var multiplier = 2
            props.answers_compare.map((kandidat) => {
                var matches = 0
                kandidat.answers.map((odpoved, i) => {
                    if (props.answers[i] && odpoved.answerID === props.answers[i]["answerID"]) {
                        var addPoints = props.answers[i]["important"] ? multiplier : 1
                        var newMatch = (matches + addPoints) > props.total ? props.total : (matches + addPoints)
                        matches = newMatch
                    }
                    return null
                })
                tmpResults.push({
                    "title": kandidat.title,
                    "matches": matches,
                    "url": kandidat.url
                })
                return null
            })
            this.setState({ results: tmpResults })
        }
    }
    openOdpovede = (i) => {
        var currentOpenResult = parseInt(i.target.id, 10)
        var modalOpen = true
        this.setState({ currentOpenResult, modalOpen })
    }
    componentDidMount = () => {
        this.calculateResults(this.props)
    }
    componentWillReceiveProps = (nextProps) => {
        this.calculateResults(nextProps)
    }

    handleClose = () => {
        this.setState({ modalOpen: false });
    };
    render() {
        return (
            <div className="content middle" style={{ marginRight: 140 }}>
                {this.state.results.length > 0 && <DisplayResults target={this} city={this.props.city} />}
            </div>
        )
    }
}

export default Results;



