import React, { Component } from 'react'
import { Link } from 'react-router-dom'

class Poslanci extends Component {
    constructor(props) {
        super(props)
        this.state = {
            channelData: [],
            channelInfo: [{
                categoryTitle: ""
            }]
        }
    }
    loadData = (city) => {
        fetch("https://huba.sk/index.php/API/kandidati/" + city + "/poslanci")
            .then(results => {
                return results.json();
            })
            .then(data => {
                const channelData = data.channelData.entries;
                const channelInfo = {
                    categoryTitle: data.channelData.categoryTitle
                };
                this.setState({ channelData, channelInfo });
            })
    }


    componentDidMount() {
        this.loadData(this.props.city)
    }

    render() {
        return (
            <div className="poslanci">
                <h2>Zoznam kandidátov na poslancov do miestneho zastupiteľstva</h2>
                <p></p>
                {this.state.channelData.map((channelEntry) => {
                    return (
                        <div key={channelEntry.url_title} className="channelEntry">
                            <h2 className={!channelEntry.cislo && "empty"}>{channelEntry.cislo > 0 && <span className="cislo">{channelEntry.cislo}</span>} {channelEntry.fulltext !== null ? <Link to={"/" + this.props.city + "/" + channelEntry.url_title}>{channelEntry.title}</Link> : channelEntry.title}</h2>
                            <p className={"space small"}>{channelEntry.summary}</p>
                        </div>
                    )
                }
                )}
                <p>&nbsp;</p>
            </div>
        )
    }
}

export default Poslanci



