import React, { Component } from 'react'
import Header from '../components/Header'
import Questionaire from '../components/Questionaire'
import Kandidati from '../components/Kandidati'
import Poslanci from '../components/Poslanci'
import Loading from '../components/Loading'

import ArrowBack from '@material-ui/icons/ArrowBack'

class Article extends Component {
	constructor(props) {
		super(props)
		this.state = {
			channelData: [],
			loading: true,
			url: null,
			kandidati: 0,
			poslanci: 0,
			channelInfo: [{
				categoryTitle: ""
			}]
		}
	}

	loadData = (url) => {
		this.setState({ loading: true })
		fetch("https://huba.sk/index.php/API" + url)
			//		fetch("/API/"+ this.props.match.params.channel + "/" + this.props.match.params.url_title + ".json")
			.then(results => {
				return results.json();
			})
			.then(data => {
				const channelData = data.channelData.entries;
				const channelInfo = {
					categoryTitle: data.channelData.categoryTitle
				}
				const kandidati = data.channelData.entries.length ? data.channelData.entries[0].kandidati : 0
				const poslanci = data.channelData.entries.length ? data.channelData.entries[0].poslanci : 0
				this.setState({ channelData, channelInfo, kandidati, poslanci })
				document.title = data.channelData.categoryTitle + " | " + data.channelData.entries[0].title + " | Komunálne voľby 2018"
			})
		this.setState({ loading: false, url: url })
		this.props.setParentState(url)
		//console.log('Single loadData from ' + url)
	}
	componentDidMount() {
		return this.state.url !== this.props.match.url ? this.loadData(this.props.match.url) : null
	}
	componentWillReceiveProps(nextProps) {
		return this.state.url !== nextProps.match.url ? this.loadData(nextProps.match.url) : null
	}
	render() {
		var questions = []
		return (
			<div className="flex">
				<Header title={this.state.channelInfo.categoryTitle} {...this.props} />
				<div className={this.props.city + " bg"}>
					<div className="articles page-container container">
						{this.state.loading && <Loading />}
						{this.state.channelData.map((channelEntry) => {
							return (
								<div key={channelEntry.url_title} className="channelEntry">
									<h2>{channelEntry.cislo > 0 && <span className="cislo">{channelEntry.cislo}</span>} <a onClick={this.props.history.goBack} className="arrow"><ArrowBack /></a> {channelEntry.title}</h2>
									{channelEntry.images && channelEntry.images.map(
										(image, key) => {
											return (
												<img key={key} src={image.image} alt={image.desc} />
											)
										}
									)}
									{channelEntry.summary && <p className={channelEntry.cislo > 0 ? "space small" : ""}>{channelEntry.summary}</p>}
									{channelEntry.otazky &&
										channelEntry.otazky.map(
											(otazka, key) => {
												var temp = {}
												temp["otazka"] = otazka.otazka
												temp["moznosti"] = otazka.odpovede
												temp["id"] = otazka.id

												questions.push(temp)
												return null
											}
										)}
									{channelEntry.otazky && <Questionaire questions={questions} city={this.props.city} cityName={this.state.channelInfo.categoryTitle} />}
									{this.state.kandidati > 0 && <Kandidati city={this.props.city}/>}
									{this.state.poslanci > 0 && <Poslanci city={this.props.city}/>}
									{channelEntry.fulltext && <div dangerouslySetInnerHTML={{ __html: channelEntry.fulltext }} ></div>}
								</div>
							)
						}
						)}
					</div>
				</div>
			</div>
		);
	}
}

export default Article;



